import React from 'react'
import PropTypes from 'prop-types'
import { useStaticQuery, graphql } from 'gatsby'

import Nav from 'react-bootstrap/Nav'
import Navbar from 'react-bootstrap/Navbar'
import NavDropdown from 'react-bootstrap/NavDropdown'

const Header = ({ sticky }) => {
  const data = useStaticQuery(graphql`
    query SiteTitleQuery {
      site {
        siteMetadata {
          title
          subTitle
        }
      }
    }
  `)

  return (
    <Navbar as='header' bg='light' expand='md' sticky={sticky ? 'top' : ''}>
      <Navbar.Brand href='/'>
        <strong>{data.site.siteMetadata.title}</strong>{' '}
        {data.site.siteMetadata.subTitle}
      </Navbar.Brand>
      <Navbar.Toggle aria-controls='basic-navbar-nav' />
      <Navbar.Collapse id='basic-navbar-nav'>
        <Nav className='mr-auto'>
          <Nav.Link href='/'>Home</Nav.Link>
          <Nav.Link href='/docs/raas/intro'>Getting Started</Nav.Link>
          <NavDropdown title='API References' id='basic-nav-dropdown'>
            <NavDropdown.Item href='/raas/api'>
              Recommendations
            </NavDropdown.Item>
            <NavDropdown.Item href='/saas/api'>Search</NavDropdown.Item>
          </NavDropdown>
        </Nav>
      </Navbar.Collapse>
    </Navbar>
  )
}

Header.propTypes = {
  sticky: PropTypes.bool,
}

Header.defaultProps = {
  sticky: true,
}

export default Header
